.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.backgroundColor{
  background-color: #f5f7fa;
  font-family: 'Courier New', monospace;
  font-size: 12px;
  text-align: center;;
}

.homescreenFormat{
  background-color: #ffffff;
  height: 1000%;
  max-width: 70%;
  margin: auto;
  margin-top: -10px;
  margin-bottom: 3%;
  
}

.logoBar{
  background-color: #141414;
  color: #ffffff;
  width: 100%;
  top: 0;
  padding: 85px 0;
}

.accHeader {
    float: right;
}

.lil {
  border-style: solid;
  border-width: 0px;
  border-color: #0000000;
  border-radius: 8px;
  background-color: #464646;
  padding-left: 7px;
  padding-right: 7px;
}

.logoHead{
  float: top;
  margin-left: 25px;
}

.forms{
  margin-top: 10px;
  padding-bottom: 20px;
}

.neg{
  color: red;
}

.pos{
  color: green;
}

.greyA{
  background: #f5f7fa;
}

.backBut{
  height: 50px;
  background:#464646;
  max-width: 70%;
  margin: auto;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-style: solid;
  border-color: #464646;
  text-align: center;
}

.imageDash{
  margin: 10px;
  text-decoration: none;
  color: #ffffff;
  margin: 10%;
}

.fontSize{
  font-size: 15px;
}

.rowSpace{
  border-color: #a9a9a9;
  border-style: solid;
  border-top-width: 0px;
  border-left-width: 0px;
  border-right-width: 0px;
  border-bottom-width: 1px;
}

.tableD{
  border-spacing: 0px;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #141414;
  color: white;
  font-family: 'Courier New', monospace;
}

.footerLinks1{
  float:right;
  margin-right: 5%;
}

.footerLinks3{
  float:center;
  margin-left: 45%;
}

.footerLinks2{
  float:left;
  margin-left: 5%;
}

.spacerForFooter{
  height: 20%;
}